<template>
  <nav class="navbar bg-dark navbar-dark text-white font-weight-bold">
    <router-link to="/" class="navbar-brand">
      <img src="../assets/logo.png" alt="" width="30" height="30" class="align-top mr-2"/>Vue.js
    </router-link>
    <div>
      <!-- <router-link to="/" class="btn btn-success btn-sm">로그인</router-link> -->
      <router-link v-if="$store.state.userId === ''" to="/menu07/auth/login" 
                   class="btn btn-info btn-sm">로그인</router-link>
      <button v-if="$store.state.userId !== ''" class="btn btn-info btn-sm" 
              v-on:click="handleLogout">로그아웃</button>
    </div>
  </nav>
</template>

<script setup>
import { useStore } from "vuex";
import { useRouter } from "vue-router";

const store = useStore();
const router = useRouter();

function handleLogout() {
  store.dispatch("deleteAuth");
  router.push("/");
}
</script>

