import { createStore } from 'vuex'

//Axis 설정 모듈 가져오기
import axiosConfig from "@/apis/axiosConfig";

//counter 상태 모듈 가져오기
import counter from "./counter";

//Store 생성 및 내보내기
export default createStore({
  //루트 상태 정의
  state: {
    userId: "",
    authToken: "",
  },

  //루트 상태 값을 읽는 메소드(Getter) 정의
  getters: {  
    getUserId(state, getters, rootState, rootGetters) {  
      /*
      console.group("Vuex Root Getters/getUserId");
      console.log("state:", state);             // root state 객체
      console.log("getters:", getters);         // root getters 객체
      console.log("rootState:", rootState);     // root stat 객체
      console.log("rootGetters:", rootGetters); // root getters 객체
      console.groupEnd();
      */
      return state.userId;
    },

    getAuthToken(state, getters, rootState, rootGetters) { 
      return state.authToken;
    },      
  },

  //루트 상태 값을 변화시키는 메소드(Setter) 정의(동기 방식)
  mutations: {
    setUserId(state, payload) {
      /* 
      console.group("Vuex Root Mutations/setuserId");
      console.log("state:", state);     // root state 객체
      console.log("payload:", payload); // 전달된 데이터
      console.groupEnd();
      */
      state.userId = payload
    },

    setAuthToken(state, payload) {
      state.authToken = payload;
    },
  },

  //작업을 수행하고 결과에 따라 상태 값을 변화시키는 메소드 정의
  actions: {
    /*
    context: {
      state,      // root state 객체
      rootState,  // root state 객체
      getters,    // root getters 객체
      rootGetters // root getters 객체
      commit,     // store.commit 함수
      dispatch,   // store.dispatch 함수     
    }
    payload: 전달된 데이터
    */
    setUserIdByAsync(context, payload) {
      /* 
      console.group("Vuex Root Actions/receiveUserIdFromApi");
      console.log("context:", context);
      console.log("payload:", payload);
      console.groupEnd();
      */
      new Promise((resolve, reject) => {
        setTimeout(() => {
          context.commit("setUserId", payload.userId);
          resolve(payload.userId);
        }, payload.duration);
      })
      .then((data) => {
        console.log(`userId 상태 변경 성공: ${data}`);
      })
      .catch((reason) => {
        console.log(`userId 상태 변경 실패: ${reason}`);
      });
    },

    loadAuth(context, payload) {
      context.commit("setUserId", sessionStorage.getItem("userId") || "");
      context.commit("setAuthToken", sessionStorage.getItem("authToken") || "");
      
      if(context.state.authToken !== "") {
      //if(context.getters.getAuthToken !== "") {
        axiosConfig.addAuthHeader(context.state.authToken);
      }
    },

    saveAuth(context, payload) {
      context.commit("setUserId", payload.userId);
      context.commit("setAuthToken", payload.authToken);
      sessionStorage.setItem("userId", payload.userId);
      sessionStorage.setItem("authToken", payload.authToken);
      axiosConfig.addAuthHeader(payload.authToken);
    },

    deleteAuth(context, payload) {
      context.commit("setUserId", "");
      context.commit("setAuthToken", "");
      sessionStorage.removeItem("userId");
      sessionStorage.removeItem("authToken");
      axiosConfig.removeAuthHeader();
    }     
  },

  modules: {
    counter
  }
})
