export default {
  //모듈의 이름을 루트 하위 네임스페이스로 설정
  namespaced: true,
  
  //상태 정의
  state: {
    count: 0
  },

  //상태 값을 읽는 메소드(Getter) 정의
  getters: {
    getCount: (state, getters, rootState, rootGetters) => {
      /* 
      console.group("Vuex Counter Getters/getCount");
      console.log("state:", state);             //모듈 state 객체
      console.log("getters:", getters);         //모듈 getters 객체
      console.log("rootState:", rootState);     //root stat 객체
      console.log("rootGetters:", rootGetters); //root getters 객체
      console.groupEnd();
      */
      return state.count;
    }  
  },

  //상태 값을 변화시키는 메소드(Setter) 정의(동기 방식)
  mutations: {
    setCount: (state, payload) => {
      /* 
      console.group("Vuex Counter Mutations/setCount");
      console.log("state:", state);       //모듈 state 객체
      console.log("payload:", payload);   //전달된 데이터
      console.groupEnd();
      */
      state.count += payload;
    }    
  },

  //작업을 수행하고 결과에 따라 상태 값을 변화시키는 메소드 정의
  actions: {
    /*
    context: {
      state,      // 모듈 state 객체
      rootState,  // root state 객체
      getters,    // 모듈 getters 객체
      rootGetters // root getters 객체    
      commit,     // store.commit 함수
      dispatch,   // store.dispatch 함수
    }
    payload: 전달된 데이터
    */  
    setCountByAsync: (context, payload) => {
      /* 
      console.group("Vuex Counter Actions/receiveCountFromApi");
      console.log("context:", context);
      console.log("payload:", payload);
      console.groupEnd();
      */
      new Promise((resolve, reject) => {
        setTimeout(() => {
          context.commit("setCount", payload.value);
          resolve(payload.value);
        }, payload.duration);
      })
      .then((data) => {
        console.log(`counter/count 상태 변경 성공: ${data}`);
      })
      .catch((reason) => {
        console.log(`counter/count 상태 변경 실패: ${reason}`);
      });
    }    
  },
};

